.skills {
  padding: 50px 0;
  background-color: #f5f5f5;
  text-align: -webkit-center;

  h2 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 2rem;
    color: #171725;
  }

  .skills-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 80%;
    max-width: 1000px;

    .skill-item {
      background-color: #fff;
      border: 1px solid #ddd;
      border-radius: 5px;
      padding: 10px 15px;
      margin: 10px;
      font-size: 1rem;
      color: #171725;
      display: grid;
      justify-items: center;
      width: 128px;
      height: 96px;

      img {
        margin-bottom: 5px;
      }

      &:hover {
        box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px, rgba(0, 0, 0, 0.05) 0px 5px 10px;
      }
    }
  }
}

@media screen and (max-aspect-ratio: 10/9) {
  .skills .skills-list {
    width: 90%;
  }
}

@media screen and (max-width: 720px) {
  .skills .skills-list {
    width: 100%;
  }
}

.fa-microchip {
  color: #11e397;
  margin-right: 5px;
}
